F9PA;QE+PA,mBAAkB;CAKrB;AAjRrB;UA8Q4B,mBAAkB;UAClB,mBAAkB;CACrB","file":"index.vue","sourcesContent":["@charset \"UTF-8\";\n/*\n@module 颜色\n*/\n/*背景色*/\n/*背景颜色名*/\n/*边框颜色*/\n/*字体颜色*/\n/*\n@module 字体大小\n*/\n/* 防止用户自定义背景颜色对网页的影响，添加让用户可以自定义字体 */\nhtml {\n  min-height: 100%;\n  -webkit-text-size-adjust: 100%;\n  -ms-text-size-adjust: 100%;\n  -webkit-overflow-scrolling: touch;\n  padding: constant(safe-area-inset-top) constant(safe-area-inset-right) constant(safe-area-inset-bottom) constant(safe-area-inset-left); }\n\n/* 内外边距通常让各个浏览器样式的表现位置不同 */\nbody,\ndiv,\ndl,\ndt,\ndd,\nul,\nol,\nli,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\npre,\ncode,\nform,\nfieldset,\nlegend,\ninput,\ntextarea,\np,\nblockquote,\nth,\ntd,\nhr,\nbutton,\narticle,\naside,\ndetails,\nfigcaption,\nfigure,\nfooter,\nheader,\nhgroup,\nmenu,\nnav,\nsection,\n* {\n  margin: 0;\n  padding: 0; }\n\n/* 重设 HTML5 标签, IE 需要在 js 中 createElement(TAG) */\narticle,\naside,\ndetails,\nfigcaption,\nfigure,\nfooter,\nheader,\nhgroup,\nmenu,\nnav,\nsection {\n  display: block; }\n\n/* HTML5 媒体文件跟 img 保持一致 */\naudio,\ncanvas,\nvideo {\n  display: inline-block;\n  *display: inline;\n  *zoom: 1; }\n\n/* 要注意表单元素并不继承父级 font 的问题 */\nbody,\nbutton,\ninput,\nselect,\ntextarea {\n  font-style: normal;\n  font-size: 0.24rem;\n  font-family: \\5FAE\\8F6F\\96C5\\9ED1, Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, tahoma, arial, \"Hiragino Sans GB\", \\5b8b\\4f53, sans-serif;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n  color: #fff; }\n\ninput,\nselect,\ntextarea {\n  font-size: 100%; }\n\n/* 去掉各Table  cell 的边距并让其边重合 */\ntable {\n  border-collapse: collapse;\n  border-spacing: 0; }\n\n/* IE bug fixed: th 不继承 text-align*/\nth {\n  text-align: inherit; }\n\n/* 去除默认边框 */\nfieldset,\nimg {\n  border: 0; }\n\n/* ie6 7 8(q) bug 显示为行内表现 */\niframe {\n  display: block; }\n\n/* 去掉 firefox 下此元素的边框 */\nabbr,\nacronym {\n  border: 0;\n  font-variant: normal; }\n\n/* 一致的 del 样式 */\ndel {\n  text-decoration: line-through; }\n\naddress,\ncaption,\ncite,\ncode,\ndfn,\nem,\nth,\nvar {\n  font-style: normal;\n  font-weight: 500; }\n\n/* 去掉列表前的标识, li 会继承 */\nol,\nul {\n  list-style: none; }\n\n/* 对齐是排版最重要的因素, 别让什么都居中 */\ncaption,\nth {\n  text-align: left; }\n\n/* 来自yahoo, 让标题都自定义, 适应多个系统应用 */\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  font-size: 100%;\n  font-weight: 500; }\n\nq:before,\nq:after {\n  content: ''; }\n\n/* 统一上标和下标 */\nsub,\nsup {\n  font-size: 75%;\n  line-height: 0;\n  position: relative;\n  vertical-align: baseline; }\n\nsup {\n  top: -0.5em; }\n\nsub {\n  bottom: -0.25em; }\n\n/* 正常链接 未访问 */\na:link {\n  text-decoration: none; }\n\n/* 鼠标悬停 */\na:hover {\n  text-decoration: none; }\n\n/* 默认不显示下划线，保持页面简洁 */\nins,\na {\n  text-decoration: none; }\n\n/* 代码字体 */\ncode,\nkbd,\npre,\nsamp {\n  font-family: monospace, serif;\n  font-size: 1em; }\n\n/* 隐藏, 通常用来与 JS 配合 */\nbody .fn-hide {\n  display: none; }\n\n/* a链接去掉点击时候的虚线*/\na,\nbutton,\ninput {\n  text-decoration: none;\n  outline: none;\n  /*ff*/\n  /*ie*/ }\n\na:hover {\n  text-decoration: none; }\n\n/*初始化系统默认样式*/\ni,\nem {\n  font-style: normal; }\n\ninput,\nselect,\ntextarea {\n  vertical-align: middle;\n  -webkit-appearance: none;\n  border-radius: 0;\n  outline: none;\n  font-family: \\5FAE\\8F6F\\96C5\\9ED1, Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, tahoma, arial, \"Hiragino Sans GB\", \\5b8b\\4f53, sans-serif;\n  color: #333; }\n\n*,\n*:after,\n*::before {\n  -moz-box-sizing: border-box;\n  -webkit-box-sizing: border-box;\n  box-sizing: border-box; }\n\n/* 清除浏览器默认行为 */\n* {\n  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);\n  -webkit-touch-callout: none; }\n\na,\nbutton {\n  -webkit-user-select: none; }\n\ninput[type=tel],\ninput[type=number],\ninput[type=password],\ninput[type=date] {\n  font-family: tahoma, arial, \"Hiragino Sans GB\", \\5b8b\\4f53, sans-serif; }\n\n::-webkit-input-placeholder {\n  color: #999;\n  opacity: 1; }\n\n:-moz-placeholder {\n  color: #999;\n  opacity: 1; }\n\n::-moz-placeholder {\n  color: #999;\n  opacity: 1; }\n\n:-ms-input-placeholder {\n  color: #999;\n  opacity: 1; }\n\ninput::-webkit-input-placeholder {\n  color: #c7c7c7; }\n\ninput::-moz-placeholder {\n  /* Mozilla Firefox 19+ */\n  color: #c7c7c7; }\n\ninput:-moz-placeholder {\n  /* Mozilla Firefox 4 to 18 */\n  color: #c7c7c7; }\n\ninput:-ms-input-placeholder {\n  /* Internet Explorer 10-11 */\n  color: #c7c7c7; }\n\n/*input[type=\"date\"]:before {\n    content: attr(placeholder);\n    color:#c7c7c7;width: 100%;\n    text-align: center;\n}*/\nbody {\n  color: #000;\n  margin: 0 auto;\n  overflow-x: hidden;\n  min-height: 100%;\n  background: #ebebeb;\n  font-family: 'Avenir', '微软雅黑', '黑体', Helvetica, sans-serif;\n  font-size: 0.24rem; }\n\na {\n  color: #666; }\n\n/*------------global-css-------------*/\n/* 清理浮动 */\n.fn-clear:after {\n  visibility: hidden;\n  display: block;\n  font-size: 0;\n  content: '';\n  clear: both;\n  height: 0; }\n\n.fn-clear {\n  zoom: 1;\n  /* for IE6 IE7 */ }\n\n/*公共间距*/\n.mt10 {\n  margin-top: .1rem; }\n\n.mt20 {\n  margin-top: .2rem; }\n\n.mb10 {\n  margin-bottom: .1rem; }\n\n.mb20 {\n  margin-bottom: .2rem; }\n\n.ml10 {\n  margin-left: .1rem; }\n\n.ml20 {\n  margin-left: .2rem; }\n\n/*浮动*/\n.left {\n  float: left; }\n\n.right {\n  float: right; }\n\n.heightFull {\n  min-height: 100%;\n  background: #ebebeb; }\n\n.contaniner {\n  position: relative;\n  min-height: 100%; }\n  .contaniner > .main {\n    min-height: 100%;\n    padding-top: .88rem;\n    padding-bottom: 1.3rem; }\n\n.com-padding, .gray-pop .pop-content, .amoutFF-wrap .title, .amoutFF-wrap .eleFee-wrap, .amoutFF-wrap .history-container .total-month {\n  padding: 0 .33rem; }\n\n.com-content, .amoutFF-wrap .title, .amoutFF-wrap .total-wrap {\n  background: #fff; }\n\n.arrow {\n  position: absolute;\n  right: .25rem;\n  top: 0; }\n  .arrow i {\n    display: block;\n    color: #bbb;\n    -webkit-transform: rotate(-90deg);\n    transform: rotate(-90deg); }\n\n.base-select {\n  line-height: .45rem;\n  position: relative;\n  border: 1px solid #cad3df;\n  border-radius: 3px;\n  padding-right: 20px; }\n  .base-select span {\n    padding: .25rem 0;\n    width: 100%;\n    height: .5rem;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n    overflow: hidden;\n    padding-left: .15rem; }\n  .base-select .arrow {\n    right: .1rem; }\n\n.base-bd {\n  border: 1px solid #d9d9d9; }\n\n/*------没有边框的选择框-----*/\n.no-bd-select {\n  width: 100%;\n  text-align: center; }\n  .no-bd-select span {\n    display: inline-block;\n    color: #353535; }\n  .no-bd-select i {\n    display: inline-block;\n    vertical-align: bottom;\n    margin-left: .1rem;\n    color: #bbb;\n    -webkit-transform: rotate(-90deg);\n    transform: rotate(-90deg); }\n\n/*---------按钮式的内容切换-----------*/\n.button-tab {\n  border: 1px solid #3a8dda;\n  border-radius: 3px;\n  display: flex; }\n  .button-tab .item {\n    flex-grow: 1;\n    line-height: .6rem;\n    text-align: center;\n    border-right: 1px solid #3a8dda;\n    position: relative; }\n    .button-tab .item:last-child {\n      border: 0; }\n    .button-tab .item.on {\n      color: #fff;\n      background: #3a8dda; }\n\n.submit {\n  height: .6rem;\n  background: #3a8dda !important;\n  color: #fff !important;\n  font-size: .24rem; }\n\n/*----------自定义-具有遮罩的弹出框----------*/\n.gray-pop {\n  width: 100%;\n  background: rgba(53, 53, 53, 0.65);\n  position: fixed;\n  height: 100%;\n  z-index: 99;\n  bottom: 0;\n  left: 0; }\n  .gray-pop .pop-content {\n    position: absolute;\n    padding-top: .33rem;\n    padding-bottom: .33rem;\n    background: #fff;\n    bottom: 0;\n    left: 0;\n    width: 100%;\n    animation: animate .1s linear; }\n    .gray-pop .pop-content.pop-middle {\n      bottom: 2.5rem; }\n\n@keyframes animate {\n  0% {\n    bottom: -2.6rem; }\n  100% {\n    bottom: 0; } }\n\n/*--------底部有边框的tab切换----------*/\n.tab-wrap {\n  line-height: .9rem;\n  position: relative; }\n  .tab-wrap span {\n    float: left;\n    text-align: center;\n    color: #a6afb6; }\n    .tab-wrap span.on {\n      color: #353535; }\n  .tab-wrap .line {\n    position: absolute;\n    height: 3px;\n    background: #3a8dda;\n    left: 0;\n    bottom: 0;\n    transition: all ease-in .1s; }\n    .tab-wrap .line.left50 {\n      left: 50%; }\n    .tab-wrap .line.left33 {\n      left: 33.3%; }\n    .tab-wrap .line.left66 {\n      left: 66.6%; }\n\n/*------常用百分比宽度-------*/\n.col-2 {\n  width: 50%; }\n\n.col-3 {\n  width: 33.3%; }\n\n.col-4 {\n  width: 25%; }\n\n/*-------热更新进度条-------*/\n#mask-wrap {\n  width: 100%;\n  height: 100%;\n  background: rgba(0, 0, 0, 0.7);\n  position: fixed;\n  top: 0;\n  left: 0;\n  z-index: 999;\n  /* Safari */\n  display: none;\n  text-align: center;\n  color: #fff;\n  font-size: .6rem;\n  align-items: center; }\n  #mask-wrap .process-con {\n    width: 80%;\n    margin: 0 auto;\n    background: #ebebeb;\n    border-radius: 15px;\n    height: .3rem;\n    position: absolute;\n    left: 10%;\n    top: 50%; }\n    #mask-wrap .process-con #current {\n      position: absolute;\n      width: 0;\n      background: #3a8dda;\n      height: 100%;\n      top: 0;\n      left: 0;\n      border-radius: 15px; }\n    #mask-wrap .process-con #process-value {\n      position: absolute;\n      top: -.5rem;\n      width: 100%;\n      text-align: center;\n      font-size: .3rem; }\n\n.amoutFF-wrap {\n  /*-------本月信息---------*/\n  /*----电度电费-------*/\n  /*-------------历史信息-----------*/ }\n  .amoutFF-wrap .title {\n    padding-top: .2rem;\n    padding-bottom: .2rem;\n    font-size: .24rem;\n    color: #353535;\n    line-height: .45rem; }\n  .amoutFF-wrap .amoutFF-content {\n    min-height: calc(100vh - 4.28rem);\n    min-height: -webkit-calc(100vh - 4.28rem); }\n  .amoutFF-wrap .now-container,\n  .amoutFF-wrap .history-container {\n    height: 8.48rem; }\n  .amoutFF-wrap .total-wrap {\n    padding-top: .2rem;\n    padding-bottom: .2rem; }\n    .amoutFF-wrap .total-wrap > div {\n      float: left;\n      width: 50%; }\n      .amoutFF-wrap .total-wrap > div:last-child {\n        padding-left: .2rem;\n        border-left: 1px solid #d9d9d9; }\n      .amoutFF-wrap .total-wrap > div .icon {\n        margin-top: .1rem; }\n        .amoutFF-wrap .total-wrap > div .icon i {\n          color: #138782;\n          font-size: .40rem; }\n          .amoutFF-wrap .total-wrap > div .icon i.icon-dian {\n            margin-left: -.05rem; }\n      .amoutFF-wrap .total-wrap > div .detail .info i {\n        vertical-align: middle;\n        display: inline-block; }\n      .amoutFF-wrap .total-wrap > div .detail .info .down {\n        color: #3fc529;\n        -webkit-transform: rotate(-45deg);\n        transform: rotate(-45deg); }\n      .amoutFF-wrap .total-wrap > div .detail .info .up {\n        color: #ff7900;\n        -webkit-transform: rotate(-145deg);\n        transform: rotate(-145deg); }\n  .amoutFF-wrap .detail-wrap .mint-tab-container-item {\n    height: 6.5rem;\n    padding: .3rem 0;\n    /*----本月基本电费信息-----*/\n    /*---本月力调电费----*/ }\n    .amoutFF-wrap .detail-wrap .mint-tab-container-item .now-info .item {\n      float: left;\n      width: 33.3%;\n      text-align: center;\n      color: #138782; }\n      .amoutFF-wrap .detail-wrap .mint-tab-container-item .now-info .item .sub-title em {\n        display: inline-block;\n        width: .3rem;\n        height: .3rem;\n        border-radius: 100%;\n        background: #3a8dda;\n        color: #fff;\n        text-align: center;\n        line-height: .3rem;\n        font-size: .16rem;\n        vertical-align: middle; }\n    .amoutFF-wrap .detail-wrap .mint-tab-container-item .tip {\n      padding: .05rem 0;\n      border-radius: 1rem;\n      box-shadow: 0 0 5px #a6afb6;\n      width: calc(100% - .66rem);\n      margin: .2rem auto 0;\n      text-align: center; }\n      .amoutFF-wrap .detail-wrap .mint-tab-container-item .tip i {\n        font-size: .4rem;\n        vertical-align: middle;\n        margin-right: .1rem; }\n      .amoutFF-wrap .detail-wrap .mint-tab-container-item .tip .icon-xiaolian {\n        color: #faba32; }\n      .amoutFF-wrap .detail-wrap .mint-tab-container-item .tip .icon-kulian {\n        color: #138782; }\n      .amoutFF-wrap .detail-wrap .mint-tab-container-item .tip span {\n        color: red;\n        font-size: 0.28rem;\n        font-weight: bold; }\n    .amoutFF-wrap .detail-wrap .mint-tab-container-item .force-wrap {\n      text-align: center;\n      color: #353535; }\n      .amoutFF-wrap .detail-wrap .mint-tab-container-item .force-wrap .statu-icon {\n        color: #faba32; }\n        .amoutFF-wrap .detail-wrap .mint-tab-container-item .force-wrap .statu-icon i {\n          font-size: 2.6rem; }\n      .amoutFF-wrap .detail-wrap .mint-tab-container-item .force-wrap .device-tlt {\n        padding-left: .15rem;\n        display: inline-block;\n        width: calc(100% - 1rem);\n        width: -webkit-calc(100% - 1rem);\n        overflow: hidden;\n        height: .4rem;\n        line-height: .4rem;\n        text-align: left; }\n      .amoutFF-wrap .detail-wrap .mint-tab-container-item .force-wrap .rate {\n        color: #138782;\n        font-weight: bold;\n        font-size: .4rem;\n        margin-bottom: .4rem; }\n      .amoutFF-wrap .detail-wrap .mint-tab-container-item .force-wrap .fee {\n        color: #faba32;\n        font-size: .36rem; }\n      .amoutFF-wrap .detail-wrap .mint-tab-container-item .force-wrap .icon-kulian,\n      .amoutFF-wrap .detail-wrap .mint-tab-container-item .force-wrap .red {\n        color: red; }\n  .amoutFF-wrap .eleFee-wrap {\n    padding-top: .2rem; }\n    .amoutFF-wrap .eleFee-wrap .info {\n      float: left;\n      width: 33.3%;\n      margin-top: 1.5rem;\n      text-align: center; }\n      .amoutFF-wrap .eleFee-wrap .info p {\n        line-height: .45rem; }\n      .amoutFF-wrap .eleFee-wrap .info.history {\n        margin-top: 1rem; }\n      .amoutFF-wrap .eleFee-wrap .info.noTime {\n        width: 100%; }\n    .amoutFF-wrap .eleFee-wrap 